.card {
  display: flex;
  background-color: #0E0E15;
  width: 954px;
  height: 252px;
  gap: 26px;
  color: white;
}
.card .image {
  width: 200px;
  height: 252px;
}
.card .image img {
  width: 100%;
  height: 100%;
}
.card .content-outer-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 9px;
  width: 728px;
  height: 252px;
}
.card .content-outer-container .content-inner-conatiner {
  width: 495px;
  height: 155px;
}
.card .content-outer-container .content-inner-conatiner .head-container {
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 495px;
  height: 73px;
  gap: 1px;
}
.card .content-outer-container .content-inner-conatiner .head-container .movie-name {
  font-size: 36px;
  line-height: 48px;
  font-weight: 400;
}
.card .content-outer-container .content-inner-conatiner .head-container p {
  font-size: 12px;
  font-weight: 300;
  line-height: 14.4px;
}
.card .content-outer-container .content-inner-conatiner .description-container {
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 495px;
  height: 70px;
  gap: 19px;
}
.card .content-outer-container .content-inner-conatiner .description-container .description {
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
}
.card .content-outer-container .content-inner-conatiner .description-container .description-content {
  font-size: 12px;
  font-weight: 300;
  line-height: 15.6px;
  text-align: start;
}
.card .content-outer-container .icons-container {
  display: flex;
  justify-content: space-between;
  width: 728px;
  height: 40px;
}
.card .content-outer-container .icons-container .left-icon-container {
  display: flex;
  width: 116px;
  height: 40px;
  gap: 6px;
}
.card .content-outer-container .icons-container .left-icon-container .like {
  width: 32px;
  height: 32px;
}
.card .content-outer-container .icons-container .left-icon-container .like img {
  width: 24px;
  height: 29px;
  position: relative;
  top: 2px;
  left: 4px;
  border: 1px;
}
.card .content-outer-container .icons-container .left-icon-container .count {
  width: 40px;
  height: 40px;
  border-radius: 32px;
  padding: 8px;
  display: flex;
  gap: 8px;
  background-color: #272732;
}
.card .content-outer-container .icons-container .left-icon-container .dislike {
  width: 32px;
  height: 32px;
  rotate: -180deg;
}
.card .content-outer-container .icons-container .left-icon-container .dislike img {
  width: 24px;
  height: 29px;
  border: 1px;
  color: #5E5E75;
}
.card .content-outer-container .icons-container .left-icon-container .bin {
  width: 26px;
  height: 29px;
}
.card .content-outer-container .icons-container .left-icon-container .bin img {
  width: 26px;
  height: 28px;
  position: relative;
  top: 0.5px;
  border: 1px;
}

.card-line {
  margin: 20px 0 20px 0;
}

