
@mixin flexCenterProp($direction){
    display: flex;
    flex-direction: $direction;
    justify-content: center;
    align-items: center;
}
nav{
    display: flex;
    justify-content: space-between;
    background-color: #0E0E15;
    color: white;
    div{
        display: flex;
        align-items: center;
        
        img{
            width: 70px;
            height: 70px;
        }
    }
    ul{
        @include flexCenterProp(row);
        list-style: none;
        li{
            margin: 1rem;
            }
        
    }

}

