
.card{
    display: flex;
    // justify-content: start;
    background-color: #0E0E15;
    width: 954px;
    height: 252px;
    gap: 26px;
    color: white;
    .image{
        width: (200px);
        height: (252px);
        img{
            width: 100%;
            height: 100%;
        }
    }
    .content-outer-container{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        // align-items: start;
        padding-right: 9px;
        width: 728px;
        height:252px;

        .content-inner-conatiner{
            width: (495px);
            height: (155px);

            .head-container{
                display: flex;
                flex-direction: column;
                align-items: start;
                width: (495px);
                height: (73px);
                gap: 1px;
                .movie-name{
                    font-size: 36px;
                    line-height: 48px;
                    font-weight:400;
                }
                p{
                    font-size: 12px;
                    font-weight: 300;
                    line-height: 14.4px;
                }
            }
            .description-container{
                display: flex;
                flex-direction: column;
                align-items: start;
                width: (495px);
                height: (70px);
                gap: 19px;

                .description{
                    font-size: 18px;
                    line-height: 18px;
                    font-weight: 400;
                }

                .description-content{
                    font-size: 12px;
                    font-weight: 300;
                    line-height: 15.6px;
                    text-align: start;
                }
            }
        }
        .icons-container{
            display: flex;
            justify-content: space-between;
            width: (728px);
            height: (40px);

            .left-icon-container{
                display: flex;
                width: (116px);
                height: (40px);
                gap: 6px;

                .like{
                    width: 32px;
                    height: 32px;
                    img{
                        width: 24px;
                        height: 29px;
                        position: relative;
                        top: 2px;
                        left: 4px;
                        border: 1px;
                    }
                }
                .count{
                    width: 40px;
                    height: 40px;
                    border-radius: 32px;
                    padding: 8px;
                    display: flex;
                    gap: 8px;
                    background-color: #272732;
                }
                .dislike{
                    width: 32px;
                    height: 32px;
                    rotate: -180deg;

                    img{
                        width: 24px;
                        height: 29px;
                        // position: relative;
                        // top: 30px;
                        // left: 28px
                        border: 1px;
                        color: #5E5E75;
                    }
                }
                .bin{
                    width: 26px;
                    height: 29px;
                    img{
                        width: 26px;
                        height: 28px;
                        position: relative;
                        top: 0.5px;
                        // left: 28px
                        border: 1px;
                    }
                }
            }
        }
    }
}
.card-line{
    margin: 20px 0 20px 0;
}
