nav {
  display: flex;
  justify-content: space-between;
  background-color: #0E0E15;
  color: white;
}
nav div {
  display: flex;
  align-items: center;
}
nav div img {
  width: 70px;
  height: 70px;
}
nav ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style: none;
}
nav ul li {
  margin: 1rem;
}

